<template>
  <div class="app">
    <img class="tgewm" :src="ewm" alt="">
    <div class="tgli">
      <p>1</p>
      <p>2 </p>
      <p>3 {{user_login}}</p>
    </div>
    <button class="copy" data-clipboard-text="http://hongbao.guotf.cn/hongbao.apk" @click="copy">
      
    </button>
  </div>

</template>
<script>
const axios = require('axios');
import Clipboard from 'clipboard';
import { Url } from '../../utils/config';
export default {
  mounted:function(){
    this.getewm();
    this.getMy();
  },
  data(){
    return{
      ewm: "",
      user_login:""
    }
  },
  methods:{
    getewm:function(){
      var _this = this;
      var id = localStorage.getItem("Id");
      axios({
        url:Url+"/apis/common/share_link",
        method:"post",
        params:{
          user_id: id
        }
      }).then(function(res){
        console.log(res);
        _this.ewm = Url+res.data.data
      })
    },
    copy:function(){
     var clipboard = new Clipboard('.copy')
    clipboard.on('success', e => {
     console.log('')
     // 
     clipboard.destroy()
    })
    clipboard.on('error', e => {
     // 
     console.log('')
     // 
     clipboard.destroy()
    })
   },

    getMy:function(){
      var _this = this;
      var id = localStorage.getItem("Id");
      axios({
        url:Url+"/apis/user/getUserInfo",
        method:"post",
        params:{
          id:id
        }
      }).then(function(res){
        console.log(res);
        if(res.data.code == 1){
          _this.user_login = res.data.data.user_login;

        }else{
          _this.$dialog.alert({
            message: res.data.msg
          });
        }
      })
    },
  }
}
</script>
<style scoped>
  .app,body{
    height: 100%;
    overflow: hidden;
  }
  .tgewm{
    display: block;
    width: 67%;
    margin: 0 auto;
  }
  .tgli{
    width: 5rem;
    margin: 0 auto;
    margin-top: 0.2rem; 
    margin-bottom: 1.7rem; 
  }
  .tgli p{
    font-size: 0.28rem;
    color: #3c3c3c;
    margin-top: 0.39rem;
  }
  .copy{
    width: 100%;
    height: 0.9rem;
    border: 0;
    background: #08C161;
    font-size: 0.28rem;
    color: #FCFCFC;
    position: relative;
    bottom: 1.8rem;
  }
</style>



